class Contenido extends React.Component{

  closeLightbox = () => {
    this.state.open = true;
  };

  render(){
    return (
      <div className="main-wrapper">
        <FirestoreProvider {...config} firebase={firebase}>
        <section className="cta-section theme-bg-light py-5">
            <div className="container text-center">
                <h2 className="heading"> DevFelipe <img src="iconcode.png" width="40px" /></h2>
                <div className="intro">Bienvenido a mí blog. Aquí encontraras temas de sistemas, espero que sea de tu interés.</div>
                <div>
                   <img src="https://user-images.githubusercontent.com/15971898/90470852-2b2fb900-e0e2-11ea-968b-b11d1cd9aeb5.png" width="40px" />
                   <img src="https://camo.githubusercontent." width="40px" />
                   <img src="https://camo.githubusercontent." width="40px" />
                </div>
            </div>
        </section>
        <section className="blog-list px-3 py-5 p-md-5">
        
        <div className="container">
       
        <FirestoreCollection path="/blog/">
          {d =>{
            var lista =  _.sortBy(d.value, 'fecha_publicacion').reverse();
            return d.isLoading ? "Cargando..." : lista.map((elemento) => (
                                                  <div className="item mb-5" key={elemento.titulo}>
                                                      <div className="media">
                                                          <div className="mr-3 img-fluid post-thumb d-none d-md-flex" >
                                                              <ModalImage
                                                                small={"assets/images/blog/"+elemento.imagen}
                                                                large={"assets/images/blog/"+elemento.imagen}
                                                              />
                                                          </div>
                                                          <div className="media-body">
                                                              <h3 className="title mb-1"><a href="blog-post.html">{elemento.titulo}</a></h3>
                                                              <div className="meta mb-1">
                                                                <label className="date" style={{marginRight:'20px'}}>
                                                                  { elemento.fecha_publicacion}
                                                                </label>
                                                                {
                                                                  elemento.tags.map((element) =>(
                                                                    <div key={element} className="badge" style={{background: '#5d6ba7',color: 'white',fontSize: '10px', marginRight:'10px', padding:'5px'}}><i className="fa fa-tag"></i> {element}</div>
                                                                  ))
                                                                }
                                                                </div>
                                                              <div className="intro">{elemento.descripcion}</div>
                                                              <Link className="more-link" to="/blog/Developer-Should">Leer más &rarr;</Link>
                                                          </div>
                                                      </div>
                                                  </div>
                                                  ));
          }}
        </FirestoreCollection>

            </div>
        </section>
        
        <Footer />
        </FirestoreProvider>
    </div>
    
    );
  }
}

